.container-session {
  padding: 0px 10% 0px 10%;
}
.container-session .title-ritm {
  font-family: "Nova Round", cursive;
  font-weight: 600;
  font-size: 24px;
  color: white;
  position: relative;
  margin: 0 auto;
  width: 220px;
}
.container-session .title-ritm:before {
  content: "";
  width: 28px;
  height: 14px;
  background-color: #018ABE;
  position: absolute;
  transform: matrix(1, 0, 0, 1, 0, 0);
  border-radius: 0px 50px;
  top: 10px;
  left: -65px;
}
.container-session .title-ritm::after {
  content: "";
  width: 28px;
  height: 14px;
  background-color: #018ABE;
  position: absolute;
  transform: matrix(-1, 0, 0, 1, 0, 0);
  border-radius: 0px 50px;
  top: 10px;
  right: -65px;
}
.container-session .block-session {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 30px;
}
.container-session .block-session .image-session {
  width: 32%;
  height: 250px;
  margin-bottom: 10px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Nova Round", cursive;
  font-weight: 500;
  font-size: 16px;
  border-radius: 10px;
  position: relative;
}
.container-session .block-session .image-session:hover .card-back {
  transform: rotateY(0deg);
  z-index: 2;
}
.container-session .block-session .image-session:hover .card-front {
  transform: rotateY(180deg);
  z-index: -1;
}
.container-session .block-session .image-session .card-front {
  z-index: 1;
  position: relative;
  height: 100%;
  backface-visibility: hidden;
  background-image: url(../../../assets/session_photo.png);
  transition: 1s;
  width: 100%;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container-session .block-session .image-session .card-back {
  z-index: 0;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  backface-visibility: hidden;
  background-image: url(../../../assets/session_photo.png);
  transform: rotateY(-180deg);
  transition: transform 1s;
  width: 100%;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

